<template>
	<!---->
	<div id="refreshContainer" class="mui-content mui-scroll-wrapper" style="height: 100%;top:244px">
		<div class="mui-scroll">
			<ul class="mui-table-view mui-table-view-chevron">
				<li class="mui-table-view-cell mui-media" v-for="topic in topics">
					<router-link :to="{ path: '/detail'}">
						<img class="mui-media-object mui-pull-left" :src="src">
						<div class="mui-media-body">
							<p class='mui-ellipsis'>{{topic.title}}</p>
						</div>
					</router-link>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
				return {
					src: require("../images/laoxie.jpg"),
					topics:""
				}
			},
			methods: {
				linkTo(page) {
					console.log(page)
					window.location.href = "#/detail"
				},
				getData(){
					var self = this;
					$.ajax({
						url:"https://cnodejs.org/api/v1/topics",
						type:"GET",
						success(data){
							console.log(data)
							self.topics = data.data
						}
					})
				}
			},
			mounted() {
				this.getData();
				mui.init({
					pullRefresh: {
						container: "#refreshContainer", //querySelectorcssid.class
						down: {
							height: 50, //,50.,
							auto: true, //,false.
							contentdown: "", //
							contentover: "", //
							contentrefresh: "...", //
							callback: function() {
									mui('#refreshContainer').pullRefresh().endPulldownToRefresh();
									console.log("hello")
								} //ajax
						}
					}
				});
			}
	}
</script>
<style>
	.mui-bar-nav~ .mui-content .mui-pull-top-pocket {
		top: 0px
	}
</style>


